.footer__wrapper {
    display: grid;
    grid-template-columns: repeat(4, 1fr);;
    grid-gap: 10px;
    grid-auto-rows: minmax(100px, auto);
    background-color: #2E5077;
}

.ft_el {
    padding: 15px;
    display: flex;
    flex-direction: column;
}

.ft_el h3{
    margin: 0;
    margin-bottom: 15px;
    text-align: center;
    color: #4DA1A9;
}

.store__date img {
    width: 100px;
    margin: 0 auto;
}

.ft_el p {
    margin: 0;
    margin-bottom: 10px;
    color: #4DA1A9;
}
.ft_el li {
    margin-bottom: 10px;
    color: #4DA1A9;
}

.social__wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
}


.social__wrapper img {
    height: 45px;
}


