.categories__wrapper {
    background-color: #4DA1A9;
    padding: 15px;
}

.categories__title{
    margin: 0;
    text-align: center;
    margin-bottom: 15px;
    color: #2E5077;
}

.categories__list-title {
    text-transform: uppercase;
    color: #2E5077;
    background-color: #F6F4F0;
    padding: 15px;
    border-radius: 15px;
}

.categories__list {
    display: flex;
    gap: 15px;
    align-items: center;
    justify-content: center;
}

.categories__item {
    font-size: 18px;
    text-transform: uppercase;
    font-weight: 600;
    color: #2E5077;
    background-color: #79D7BE;
    padding: 8px 12px;
    border-radius: 10px;
    outline: 2px solid #79D7BE;
    outline-offset: 3px;
}

.category__img--wrapper {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
    padding: 35px;
    box-shadow: 0px 0px 38px -7px rgba(0,0,0,0.77);
    border-radius: 15px;
}

.category__img--wrapper img {
    max-width: 100px;
    width: 100%;
}

.category__item {
    width: 150px;
    height: 150px;
    border-radius: 15px;
    display: flex;
    justify-content: center;
}


.category__item-title {
    margin: 0;
    margin-bottom: 15px;
    text-align: center;
    text-transform: uppercase;
    font-weight: 600;
    color: #F6F4F0;
}