.products__wrapper {
    background-color: #4DA1A9;
    padding: 15px;
}

.products__title {
    margin: 0;
    margin-bottom: 10px;
    color: #2E5077;
    text-align: center;
}

.products__header img {
    margin-bottom: 10px;
}

.products__old-price {
    color: #BFBBA9;
    font-weight: 600;
    text-decoration: line-through red;
}

.products__actual-price {
    font-weight: 600;
    color: #2E5077;
}

.products__list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    grid-gap: 10px;
    grid-auto-rows: minmax(100px, auto);
}

.products__item {
    padding: 10px;
    border-radius: 10px;
    background-color: #F6F4F0;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}

.products__add-cart{
    padding: 8px 12px;
    background-color: #2E5077;
    color: #BFBBA9;
    border-radius: 15px;
    transition: opacity, 0.3s ease-in-out;
}

.products__add-cart:hover {
    opacity: 0.8;
}

