.header__wrapper{
    background-color: #2E5077;
    padding: 15px;
    display: flex;
    align-items: center;
    gap: 20px;
}

.header__logo {
    max-width: 100px;
    border-radius: 15px;
}

.header__category-btn {
    display: flex;
    align-items: center;
    gap: 10px;
    color: #4DA1A9;
}

.header__category-btn img {
    max-width: 32px;
}

.header__search-wrapper{
    position: relative;
    display: flex;
    width: 100%;
}

.header__search{
    border: 1px solid #4DA1A9;
    width: 100%;
    padding: 10px;
    border-radius: 10px;
    color: #F6F4F0;
}

.header__search:focus {
    background-color: #4DA1A9;
    border: none;
    outline: 1px solid #4DA1A9;
    outline-offset: 3px;
}

.header__search::placeholder {
    color: #F6F4F0;
}

.header__search-btn {
    position: absolute;
    right: 10px;
    top: 10px;
    max-width: 30px;
}

.header__shop-cart {
    width: 45px;
}

.close__categories, .hiden {
    display: none;
}

.show__category, .show {
    display: block;
}

.header__shop-cart-wrapper {
    position: relative;
}

.header__shop-cart-count{
    position: absolute;
    top: -10px;
    right: -10px;
    background-color: #D84040;
    padding: 3px;
    border-radius: 49%;
    width: 16px;
    height: 16px;
    text-align: center;
    color: #F6F4F0;
    font-size: 14px;
    font-weight: 600;
}