.search {
    padding: 15px;
    max-height: 300px;
    height: 100%;
    overflow: overlay;
    background-color: #66D2CE;
    scrollbar-color: transparent transparent;
}


.search__list {
    display: flex;
    flex-direction: column;
    gap: 10px;
}


.search__products-item {
    display: flex;
    gap: 20px;
    border: 1px solid #2E5077;
    padding: 8px;
    border-radius: 8px;
}

.search__products-item h4 {
    text-align: left;
}

.search__products-item img {
    width: 50px;
}