.cart__wrapper {
    padding-top: 17px;
}

.cart{
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.cart__product{
    display: flex;
    justify-content: space-between;
    gap: 15px;
    padding: 10px;
    border: 2px solid #4DA1A9;
    border-radius: 15px;
}

.cart__product-header, .cart__product-footer {
    display: flex;
    align-items: center;
    gap:20px;
}

.cart__product-header {
    max-width: 70%;
}

.cart__product-img img {
    max-width: 100px;
}

.cart__product-price {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

.cart__product-price span {
    font-weight: 600;
}

.cart__product-count {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

.cart__product-count span {
    font-weight: 600;
}

.cart__product-sum{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

.cart__product-sum span {
    font-weight: 600;
}

.cart__product-change-count{
    padding: 10px;
    display: flex;
    gap: 10px;
    align-items: center;
    border: 2px solid #4DA1A9;
    border-radius: 10px;
}

.cart__product-change-count button {
    border: none;
    font-size: 20px;
    color: #2E5077;
    cursor: pointer;
}

.delete{
    max-width: 30px;
    cursor: pointer;
}

.cart__total-sum p{
    margin: 0;
    font-size: 22px;
    font-weight: 600;
    color: #2E5077;
    text-align: right;
}

.hiden {
    display: none;
}

.cart__empty {
    font-size: 24px;
    font-weight: 600;
    text-align: center;
    color: #2E5077;
}