.product {
    padding: 15px;
    background-color: #F6F4F0;
    box-shadow: 0px 0px 38px -7px rgba(0,0,0,0.77);
}

.product__wrapper {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
    grid-gap: 10px;
    grid-auto-rows: minmax(100px, auto);
}

.back__btn {
    background-color: #4DA1A9;
    border: none;
    padding: 8px 12px;
    border-radius: 10px;
    outline: 2px solid #4DA1A9;
    outline-offset: 3px;
    margin-bottom: 15px;
    color: #F6F4F0;
    font-weight: 600;
    cursor: pointer;
    transition: opacity 0.3s ease-in-out;
}

.back__btn:hover {
    opacity: 0.8;
}

.product__title {
    font-size: 20px;
    color: #2E5077;
}

.product__info span {
    font-weight: 600;
    color: #2E5077;
    font-size: 20px;
}

.product__info {
    font-size: 18px;
    margin: 0;
    margin-bottom: 10px;
    color: #2E5077;
}

