@media screen and (max-width: 860px) {
    .special__ofert {
        flex-direction: column;
    }

    .black__friday, .credit {
        width: 100%;
    }

    .footer__wrapper {
        grid-template-columns: repeat(2, 1fr);
    }

    .categories__list {
        flex-wrap: wrap;
    }
}

@media screen and (max-width: 450px) {
    .top__products-title {
        font-size: 12px;
    }

    .top__products-desc {
        font-size: 12px;
    }

    .products__title {
        font-size: 14px;
    }


    .credit__condition {
        flex-direction: column;
    }

    .cart__wrapper{
        padding: 17px;
    }

    .cart__product-header {
        max-width: 100%;
    }

    .cart__product {
        flex-direction: column;
    }

    .footer__wrapper {
        grid-template-columns: repeat(1, 1fr);
    }
}