.similar__list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    grid-gap: 10px;
    grid-auto-rows: minmax(100px, auto);
    box-shadow: 0px 0px 38px -7px rgba(0,0,0,0.77);
}

.similar__item {
    padding: 10px;
    border-radius: 10px;
    background-color: #F6F4F0;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}

.similar__item p {
    color: #2E5077;
    font-weight: 600;
}

.similar__title {
    text-align: center;
    color: #2E5077;
}