.top__products-wrapper {
    padding: 15px;
}

.top__products-product {
    display: flex;
    border-radius: 15px;
    box-shadow: 0px 0px 38px -7px rgba(0,0,0,0.77);
    padding: 15px;
}

.top__products-title {
    color: #2E5077;
}

.top__products-old-price {
    color: #BFBBA9;
    font-weight: 600;
    text-decoration: line-through red;
}

.top__products-actual-price {
    font-weight: 600;
    color: #2E5077;
}

.top__products--add-cart{
    padding: 8px 12px;
    background-color: #2E5077;
    color: #BFBBA9;
    border-radius: 15px;
    transition: opacity, 0.3s ease-in-out;
}

.top__products--add-cart:hover {
    opacity: 0.8;
}

.top__products-footer {
    padding: 10px;
    color: #2E5077;
    width: 60%;
}

.top__products-header {
    width: 40%;
}

.top__products-footer span {
    font-weight: 600;
}